import { useEffect, useState } from 'react';

const MY_APP_NAME = 'hwahae';

const IN_APP = 'inapp';

const WHALE_APP_NAMES = ['iPhone', 'Android'].map(
  (platform) => `${platform}(.*)Whale`,
);
const DAUM_APP_NAMES = ['Apps', 'Device\\/mobile'].map((name) => `Daum${name}`);
const FACEBOOK_APP_NAMES = ['_IAB', '4A', 'AN', 'IOS', 'SS'].map(
  (kind) => `FB${kind}`,
);

const FAMOUS_APP_NAMES = [
  'NAVER',
  'KAKAOTALK',
  'Snapchat',
  'Line',
  'WirtschaftsWoche',
  'Thunderbird',
  'Instagram',
  'everytimeApp',
  'WhatsApp',
  'Electron',
  'wadiz',
  'AliApp',
  'zumapp',
  'kakaostory',
  'band',
  'twitter',
  ...WHALE_APP_NAMES,
  ...DAUM_APP_NAMES,
  ...FACEBOOK_APP_NAMES,
];

const IS_IN_APP_BROWSER_PATTERN = [MY_APP_NAME, IN_APP, FAMOUS_APP_NAMES].join(
  '|',
);

export interface DeviceInfo {
  isMobile: boolean;
  isInAppBrowser: boolean;
  isInApp: boolean;
}

type Return = DeviceInfo | null;

const useDeviceInfo = (): Return => {
  const [deviceInfo, setDeviceInfo] = useState<Return>(null);
  useEffect(() => {
    const { userAgent } = window?.navigator ?? {};
    if (!userAgent) {
      setDeviceInfo(null);
    }

    const isMobile = Boolean(userAgent.match(/mobile/i));
    const isInAppBrowser = Boolean(
      userAgent.match(new RegExp(IS_IN_APP_BROWSER_PATTERN, 'i')),
    );
    const isInApp = Boolean(userAgent.match(new RegExp(MY_APP_NAME, 'i')));
    setDeviceInfo({ isMobile, isInAppBrowser, isInApp });
  }, []);

  return deviceInfo;
};

export default useDeviceInfo;
