import { css } from '@emotion/react';

// TODO: theme 으로 이동
const style = {
  h1: css`
    font-style: normal;
    font-weight: 900;
    font-size: 40px;
    letter-spacing: -0.2px;
    line-height: 40px;
  `,
  h2: css`
    font-style: normal;
    font-weight: 900;
    font-size: 32px;
    letter-spacing: -0.2px;
    line-height: 36px;
  `,
  h3: css`
    font-style: normal;
    font-weight: 700;
    font-size: 24px;
    letter-spacing: -0.2px;
    line-height: 30px;
  `,
  h4: css`
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    letter-spacing: -0.2px;
    line-height: 24px;
  `,

  title: css`
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    letter-spacing: -0.2px;
    line-height: 24px;
  `,
  subtitle: css`
    font-style: normal;
    font-weight: 300;
    font-size: 16px;
    letter-spacing: -0.2px;
    line-height: 24px;
  `,
  body: css`
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    letter-spacing: -0.2px;
    line-height: 20px;
  `,
  caption: css`
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    letter-spacing: -0.2px;
    line-height: 16px;
  `,
  badge: css`
    font-style: normal;
    font-weight: 700;
    font-size: 12px;
    line-height: 14px;
    letter-spacing: -0.2px;
  `,
};

const variants = [
  'h1',
  'h2',
  'h3',
  'h4',
  'title',
  'subtitle',
  'body',
  'caption',
  'badge',
] as const;
export type Variant = (typeof variants)[number];

interface TypographyProps {
  children: React.ReactNode;
  variant?: Variant;
}

const Typography = ({
  children,
  variant = 'body',
  ...others
}: TypographyProps): JSX.Element => {
  return (
    <span css={style[variant]} {...others}>
      {children}
    </span>
  );
};

export default Typography;
