import { useCallback } from 'react';

import { ROOT_PATH } from 'src/constants/resouce';
import useDeviceInfo from 'src/hooks/useDeviceInfo';

const isHttpProtocol = (href: string) =>
  Boolean(href.match(/^(http|https):\/\//i));
const isInRoot = (href: string) => Boolean(href.startsWith(ROOT_PATH));
const getHrefExcludeRootPath = (href: string) => {
  if (!isInRoot(href)) {
    return href;
  }
  return href.substring(ROOT_PATH.length);
};

const getWebviewLink = (href: string): string => `hwahae://webview?url=${href}`;

interface LinkButtonProps {
  children?: React.ReactNode;
  href?: string;
}

const LinkButton = ({
  children,
  href,
  ...others
}: LinkButtonProps): JSX.Element | null => {
  const { isInApp = false } = useDeviceInfo() ?? {};
  const getHref = useCallback(
    (href: string) => {
      if (!isInApp) {
        return getHrefExcludeRootPath(href);
      }
      if (!isHttpProtocol(href)) {
        return href;
      }
      return getWebviewLink(href);
    },
    [isInApp],
  );

  const handleClick = () => {
    console.log('LinkButton', 'handleClick', 'original', href);
    if (!href) {
      return;
    }
    const next = getHref(href);
    console.log('LinkButton', 'handleClick', 'modified', next);
    window.location.href = next;
  };

  if (!children) {
    return null;
  }

  return (
    <button type="button" onClick={handleClick} {...others}>
      {children}
    </button>
  );
};

export default LinkButton;
