import {
  AnimatedInView,
  ELASTIC_OPTIONS,
  STEP_UNIT,
} from 'src/components/animated';
import { Typography } from 'src/components/dataDisplay';
import { Stack } from 'src/components/layout';
import type { LinkableContent } from 'src/hooks/useMagazineItems';

interface AnimatedMarkerContentWrapperProps {
  children: React.ReactNode;
}
const AnimatedMarkerContentWrapper = ({
  children,
}: AnimatedMarkerContentWrapperProps): JSX.Element => {
  return (
    <AnimatedInView
      options={{
        ...ELASTIC_OPTIONS,
        config: {
          ...ELASTIC_OPTIONS.config,
          duration: STEP_UNIT,
        },
      }}
    >
      <Stack>{children}</Stack>
    </AnimatedInView>
  );
};

interface AnimatedMarkerContentProps
  extends Pick<LinkableContent, 'title' | 'description'> {}
const AnimatedMarkerContent = ({
  title,
  description,
}: AnimatedMarkerContentProps): JSX.Element => {
  if (!description) {
    return (
      <AnimatedMarkerContentWrapper>
        <Typography variant={title.variant}>{title.content}</Typography>
      </AnimatedMarkerContentWrapper>
    );
  }
  return (
    <AnimatedMarkerContentWrapper>
      <Typography variant={title.variant}>{title.content}</Typography>
      <Typography variant={description.variant}>
        {description.content}
      </Typography>
    </AnimatedMarkerContentWrapper>
  );
};

export default AnimatedMarkerContent;
