import { css } from '@emotion/react';

import { FullHeightBox } from 'src/components/layout';

interface PaperProps {
  children: React.ReactNode;
  backgroundUrl?: string;
}

const scrollableItem = css`
  position: relative;
  scroll-snap-stop: always;
  scroll-snap-align: center;
`;

const background = (url: PaperProps['backgroundUrl']) => css`
  ${url
    ? ` background-image:  url(${url});
    background-repeat: no-repeat;
    background-size: auto 100% ; 
    background-position: center;`
    : `background: #ffffff;`}
`;

const Paper = ({
  children,
  backgroundUrl,
  ...others
}: PaperProps): JSX.Element => {
  return (
    <FullHeightBox
      css={[scrollableItem, background(backgroundUrl)]}
      {...others}
    >
      {children}
    </FullHeightBox>
  );
};

export default Paper;
