export const adjustTop = (
  originTop: number,
  currentHeight: number,
  baseHeight: number,
): number | null => {
  if (currentHeight <= 0 || baseHeight <= 0) {
    return null; // error
  }

  const diff = currentHeight - baseHeight;
  const adjustment = diff === 0 ? 0 : (diff * originTop) / baseHeight;
  return originTop + adjustment;
};
const getRate = (current: number, base: number): number | null => {
  if (current <= 0 || base <= 0) {
    return null; // error
  }
  const diff = current - base;
  return diff === 0 ? 1 : current / base;
};
const adjustmentForX = (
  originLeft: number,
  currentWidth: number,
  baseWidth: number,
  rate: number,
): number | null => {
  if (currentWidth <= 0 || baseWidth <= 0 || rate <= 0) {
    return null; // error
  }
  const widthDiff = currentWidth / rate - baseWidth;
  return widthDiff === 0 ? 0 : widthDiff / 2;
};

export const adjustLeft = (
  originLeft: number,
  currentWidth: number,
  baseWidth: number,
  currentHeight: number,
  baseHeight: number,
): number | null => {
  if (
    currentWidth <= 0 ||
    baseWidth <= 0 ||
    currentHeight <= 0 ||
    baseHeight <= 0
  ) {
    return null; // error
  }

  const rate = getRate(currentHeight, baseHeight);
  if (rate === null || rate <= 0) {
    return null; // error
  }

  const width = currentWidth > currentHeight ? currentHeight : currentWidth; // 에외 처리
  const adjustment = adjustmentForX(originLeft, width, baseWidth, rate);
  if (adjustment === null) {
    return null; // error
  }

  return (originLeft + adjustment) * rate;
};
export const adjustRight = (
  originLeft: number,
  currentWidth: number,
  baseWidth: number,
  baseRightMargin: number,
  currentHeight: number,
  baseHeight: number,
): number | null => {
  if (
    currentWidth <= 0 ||
    baseWidth <= 0 ||
    currentHeight <= 0 ||
    baseHeight <= 0
  ) {
    return null; // error
  }

  const rate = getRate(currentHeight, baseHeight);
  if (rate === null || rate <= 0) {
    return null; // error
  }

  const width = currentWidth > currentHeight ? currentHeight : currentWidth; // 에외 처리
  const adjustment = adjustmentForX(originLeft, width, baseWidth, rate);

  if (adjustment === null) {
    return null; // error
  }

  const right = baseWidth - originLeft - baseRightMargin;
  return (right + adjustment) * rate;
};
