export const WORDLE = 'wordle';
export const WORDLE2 = 'wordle2';
const WORDLE_LINK = 'https://gyuusik.github.io/';
const GAMES = [WORDLE];
export type Game = (typeof GAMES)[number];

const getWebviewLink = (link: string): string => `hwahae://webview?url=${link}`;
const getWebviewDeepLink = (link: string): string =>
  `hwahae.link://webview?url=${link}`;

const useGameLink = (kind: Game): string => {
  switch (kind) {
    case WORDLE2:
      return getWebviewDeepLink(WORDLE_LINK);
    case WORDLE:
    default:
      return getWebviewLink(WORDLE_LINK);
  }
};

export default useGameLink;
