import { css } from '@emotion/react';

const ROW = 'row';
const ROW_REVERSE = 'row-reverse';
const COLUMN = 'column';
const COLUMN_REVERSE = 'column-reverse';

const DIRECTIONS = [ROW, ROW_REVERSE, COLUMN, COLUMN_REVERSE] as const;
export type Direction = (typeof DIRECTIONS)[number];

const BASELINE = 'baseline';
const NORMAL = 'normal';
const STRETCH = 'stretch';
const CENTER = 'center';
const START = 'flex-start';
const END = 'flex-end';

const ALIGN_ITEMS = [NORMAL, BASELINE, STRETCH, CENTER, START, END] as const;
export type AlignItems = (typeof ALIGN_ITEMS)[number];

const SPACE_BETWEEN = 'space-between';
const JUSTIFY_CONTENT = [NORMAL, SPACE_BETWEEN, START, END, CENTER] as const;
export type JustifyContent = (typeof JUSTIFY_CONTENT)[number];

export interface StackProps {
  direction?: Direction;
  spacing?: number;
  alignItems?: AlignItems;
  justifyContent?: JustifyContent;
  children: React.ReactNode;
}

const wrapper = (
  direction: StackProps['direction'] = COLUMN,
  spacing = 0,
  alignItems = NORMAL,
  justifyContent = NORMAL,
) => css`
  display: flex;
  flex-direction: ${direction};
  gap: ${spacing}px;
  align-items: ${alignItems};
  justify-content: ${justifyContent};
`;

// flex-direction: column 을 기본으로 하며
// flex-start, flext-end 등 사용하는 property 만 제공하는 정렬을 위한 Flex Box
const Stack = ({
  children,
  direction,
  spacing,
  alignItems,
  justifyContent,
  ...others
}: StackProps): JSX.Element => {
  return (
    <div
      css={wrapper(direction, spacing, alignItems, justifyContent)}
      {...others}
    >
      {children}
    </div>
  );
};

export default Stack;
