import type { LinkableContent } from 'src/hooks/useMagazineItems';

import LeftIconMarker from './LeftIconMarker';
import RightIconMarker from './RightIconMarker';

interface MarkerProps extends LinkableContent {}
const Marker = ({ direction, ...others }: MarkerProps): JSX.Element => {
  // direction 는 컨텐트의 방향
  // e.g. 컨텐트 방향이 왼쪽인 경우 오른쪽에 Icon 이 존재하는 Maker 를 사용한다.
  if (direction === 'left') {
    return <RightIconMarker {...others} />;
  }
  return <LeftIconMarker {...others} />;
};

export default Marker;
