import { Typography } from 'src/components/dataDisplay';
import type { ContentAndVariant } from 'src/hooks/useMagazineItems';

interface TypographiesProps {
  contentAndVariant?: ContentAndVariant;
  name: string;
  isUpperCase?: boolean;
}
const Typographies = ({
  contentAndVariant,
  name,
  isUpperCase = false,
  ...others
}: TypographiesProps): JSX.Element | null => {
  if (!contentAndVariant) {
    return null;
  }

  const { content, variant } = contentAndVariant;
  return (
    <div>
      {content.split('\n').map((text, index) => {
        const key = `${name}-${index}`;
        return (
          <Typography key={key} variant={variant} {...others}>
            {isUpperCase ? text.toUpperCase() : text}
          </Typography>
        );
      })}
    </div>
  );
};

export default Typographies;
