import { css } from '@emotion/react';
import { useEffect, useState } from 'react';

import { LinkButton } from 'src/components/navigation';
import { BASE_HEIGHT, BASE_WIDTH } from 'src/constants/styles';
import type { LinkableContent } from 'src/hooks/useMagazineItems';

import AnimatedMarkerContent from './AnimatedMakerContent';
import MarkerIcon from './MarkerIcon';
import StackWithDynamicTop from './StackWithDynamicTop';
import { adjustLeft } from './utils';

const lookLikeContents = css`
  display: contents;
  color: inherit;
`;

const leftStyle = (left: number) => css`
  left: ${left}px;
`;

interface RightMarkerProps extends Omit<LinkableContent, 'direction'> {}

const LeftIconMarker = ({
  position,
  title,
  description,
  link,
}: RightMarkerProps): JSX.Element | null => {
  const isMint = Boolean(link);
  const [currentLeft, setCurrentLeft] = useState<number | null>(null);
  const onResize = () => {
    setCurrentLeft(
      adjustLeft(
        position.left,
        window.innerWidth,
        BASE_WIDTH,
        window.innerHeight,
        BASE_HEIGHT,
      ),
    );
  };
  useEffect(() => {
    window.addEventListener('resize', onResize);
    onResize();
    return () => {
      window.removeEventListener('resize', onResize);
    };
  }, [onResize]);

  if (currentLeft === null) {
    return null;
  }
  return (
    <LinkButton href={link} css={lookLikeContents}>
      <StackWithDynamicTop top={position.top} css={leftStyle(currentLeft)}>
        <MarkerIcon isMint={isMint} />
        <AnimatedMarkerContent title={title} description={description} />
      </StackWithDynamicTop>
    </LinkButton>
  );
};

export default LeftIconMarker;
