export const MEDIHEAL = 'mediheal';
export const BRAND_NAMES = [MEDIHEAL] as const;
export type BrandName = (typeof BRAND_NAMES)[number];

export const getBrandName = (name: string = MEDIHEAL): BrandName => {
  return BRAND_NAMES.includes(name as BrandName)
    ? (name as BrandName)
    : MEDIHEAL;
};
