import { css } from '@emotion/react';

import type { LayoutType } from 'src/hooks/useMagazineItems';
import {
  CENTER,
  CENTER_BOTTOM,
  LEFT_BOTTOM,
  LEFT_TOP,
  RIGHT_BOTTOM,
} from 'src/hooks/useMagazineItems';

const leftBottom = {
  contentLayout: css`
    padding-left: 20px;
    padding-bottom: 40px;
    justify-content: flex-end;
  `,
  titleStyle: css`
    display: block;
  `,
  descriptionStyle: css`
    display: block;
  `,
};

const leftTop = {
  contentLayout: css`
    padding-left: 20px;
    padding-top: 40px;
    justify-content: flex-start;
  `,
  titleStyle: css`
    display: block;
  `,
  descriptionStyle: css`
    display: block;
  `,
};

const center = {
  contentLayout: css`
    width: 100%;
    align-items: center;
    justify-content: center;
  `,
  titleStyle: css`
    display: flex;
    justify-content: center;
  `,
  descriptionStyle: css`
    display: flex;
    justify-content: center;
  `,
};

const rightBottom = {
  contentLayout: css`
    width: 100%;
    padding-right: 20px;
    padding-bottom: 40px;
    justify-content: flex-end;
    align-items: flex-end;
  `,
  titleStyle: css``,

  descriptionStyle: css`
    display: flex;
    justify-content: flex-end;
  `,
};

const centerBottom = {
  contentLayout: css`
    width: 100%;
    padding-bottom: 40px;
    justify-content: end;
    align-items: center;
  `,

  titleStyle: css`
    display: flex;
    justify-content: center;
  `,

  descriptionStyle: css`
    display: flex;
    justify-content: center;
  `,
};

export const getContainerStyle = (type: LayoutType) => {
  switch (type) {
    case LEFT_BOTTOM:
      return leftBottom;
    case CENTER_BOTTOM:
      return centerBottom;
    case RIGHT_BOTTOM:
      return rightBottom;
    case CENTER:
      return center;
    case LEFT_TOP:
    default:
      return leftTop;
  }
};
