import { css } from '@emotion/react';
import { useEffect, useState } from 'react';

import { Stack } from 'src/components/layout';
import { BASE_HEIGHT } from 'src/constants/styles';

import { adjustTop } from './utils';

const wrapper = (top: number) => css`
  position: absolute;
  top: ${top}px;
`;

interface MakerStackProps {
  top: number;
  children: React.ReactNode;
}
const StackWithDynamicTop = ({
  top,
  children,
  ...others
}: MakerStackProps): JSX.Element | null => {
  const [currentTop, setCurrentTop] = useState<number | null>(null);
  const onResize = () => {
    setCurrentTop(adjustTop(top, window.innerHeight, BASE_HEIGHT));
  };
  useEffect(() => {
    window.addEventListener('resize', onResize);
    onResize();
    return () => {
      window.removeEventListener('resize', onResize);
    };
  }, [onResize]);

  if (currentTop === null) {
    return null;
  }

  return (
    <Stack
      direction="row"
      alignItems="center"
      spacing={8}
      css={wrapper(currentTop)}
      {...others}
    >
      {children}
    </Stack>
  );
};

export default StackWithDynamicTop;
