import { css } from '@emotion/react';

const wrapper = css`
  flex: 0 0 100%;
`;

interface FullWidthBoxProps {
  children: React.ReactNode;
}
const FullWidthBox = ({
  children,
  ...others
}: FullWidthBoxProps): JSX.Element => {
  return (
    <div css={wrapper} {...others}>
      {children}
    </div>
  );
};

export default FullWidthBox;
