import type { IconBaseProps } from './IconBase';
import IconBase from './IconBase';

const DEFAULT: Omit<IconBaseProps, 'src'> = {
  alt: 'brand-logo',
  width: 161,
  height: 24,
} as const;

interface BrandLogoProps extends Pick<IconBaseProps, 'src'> {}

const BrandLogo = ({ src }: BrandLogoProps): JSX.Element => (
  <IconBase {...DEFAULT} src={src} />
);

export default BrandLogo;
