import { useRouter } from 'next/router';

import { IMAGES_PATH } from 'src/constants/resouce';

import type { BrandName } from './commons';
import { getBrandName } from './commons';

interface Return {
  name: BrandName;
  readableName: string;
  logoUrl: string;
}

const useProductInfo = (): Return => {
  // TODO: import { camelizeKeys } from 'humps'; 적용 필요 parma 은 snake 다.
  const router = useRouter();
  const name = getBrandName(router.query?.brand_name as string);
  const readableName = '메디힐';
  const logoUrl = `${IMAGES_PATH}${name}/logo.svg`;
  return {
    name,
    readableName,
    logoUrl,
  };
};

export default useProductInfo;
