export const LEFT_TOP = 'left-up';
export const CENTER = 'center';
export const RIGHT_BOTTOM = 'right-bottom';
export const CENTER_BOTTOM = 'center-bottom';

const LAYOUT_TYPES = [
  INTRO,
  LEFT_BOTTOM,
  LEFT_TOP,
  CENTER,
  RIGHT_BOTTOM,
  CENTER_BOTTOM,
] as const;
export type LayoutType = (typeof LAYOUT_TYPES)[number];
export type NormalLayoutType = Exclude<LayoutType, typeof INTRO>;

const LINK_DIRECTIONS = ['left', 'right'] as const;
export type LinkDirection = (typeof LINK_DIRECTIONS)[number];

interface Position {
  left: number;
  top: number;
}

export interface ContentAndVariant {
  content: string;
  variant: Variant;
}

export interface BadgeContent extends ContentAndVariant {
  hasBorder?: boolean;
}

interface AbstractContent {
  title: ContentAndVariant;
  description?: ContentAndVariant;
}

export interface StaticContent extends AbstractContent {
  badge?: BadgeContent;
}

export interface LinkableContent extends AbstractContent {
  position: Position;
  direction: LinkDirection;
  link?: string;
}

interface AbstractMagazineItem {
  imageUrl?: string;
}

export type IntroItem = {
  layoutType: typeof INTRO;
} & AbstractMagazineItem;

export interface NormalMagazineItem extends AbstractMagazineItem {
  layoutType: NormalLayoutType;
  staticContent: StaticContent;
  linkableContents?: Array<LinkableContent>;
}

export type MagazineItem = IntroItem | NormalMagazineItem;

