import { css } from '@emotion/react';

import { Typography } from 'src/components/dataDisplay';
import type { BadgeContent } from 'src/hooks/useMagazineItems';

const border = css`
  border: 1px solid #ffffff;
  border-radius: 100px;
  padding: 4px 8px;
  width: fit-content;
`;

interface BadgeProps extends Omit<BadgeContent, 'content'> {
  children: React.ReactNode;
}

const Badge = ({
  children,
  hasBorder = false,
  variant,
}: BadgeProps): JSX.Element | null => {
  if (!children) {
    return null;
  }
  return (
    <div css={hasBorder && border}>
      <Typography variant={variant}>{children}</Typography>
    </div>
  );
};

export default Badge;
