import { css } from '@emotion/react';

import { PlusIcon } from 'src/components/icons';

const mint = css`
  color: #5edfdf;
`;

const transparent = css`
  color: transparent;
`;
const getIconStyle = (hasColor: boolean) => (hasColor ? mint : transparent);

interface PlusProps {
  isMint?: boolean;
}
const MarkerIcon = ({ isMint = false }: PlusProps): JSX.Element => {
  return <PlusIcon css={getIconStyle(isMint)} />;
};

export default MarkerIcon;
