import { css } from '@emotion/react';
import { useEffect, useState } from 'react';

import { LinkButton } from 'src/components/navigation';
import { BASE_HEIGHT, BASE_WIDTH, RIGHT_MARGIN } from 'src/constants/styles';
import type { LinkableContent } from 'src/hooks/useMagazineItems';

import AnimatedMarkerContent from './AnimatedMakerContent';
import MarkerIcon from './MarkerIcon';
import StackWithDynamicTop from './StackWithDynamicTop';
import { adjustRight } from './utils';

const lookLikeContents = css`
  display: contents;
  color: inherit;
`;

const rightStyle = (right: number) => css`
  right: ${right}px;
`;

interface LeftMarkerProps extends Omit<LinkableContent, 'direction'> {}

const RightIconMarker = ({
  position,
  title,
  description,
  link,
}: LeftMarkerProps): JSX.Element | null => {
  const isMint = Boolean(link);
  const [currentRight, setCurrentRight] = useState<number | null>(null);
  const onResize = () => {
    setCurrentRight(
      adjustRight(
        position.left,
        window.innerWidth,
        BASE_WIDTH,
        RIGHT_MARGIN,
        window.innerHeight,
        BASE_HEIGHT,
      ),
    );
  };
  useEffect(() => {
    window.addEventListener('resize', onResize);
    onResize();
    return () => {
      window.removeEventListener('resize', onResize);
    };
  }, [onResize]);

  if (currentRight === null) {
    return null;
  }
  return (
    <LinkButton href={link} css={lookLikeContents}>
      <StackWithDynamicTop top={position.top} css={rightStyle(currentRight)}>
        <AnimatedMarkerContent title={title} description={description} />
        <MarkerIcon isMint={isMint} />
      </StackWithDynamicTop>
    </LinkButton>
  );
};

export default RightIconMarker;
