import { IMAGES_PATH } from '../constants/resouce';
import type { BrandName } from './commons';

interface Return {
  productName: string;
  productImage: string;
  reason: string;
}

interface UseBestFitProduct {
  brandName: BrandName;
  productId: number;
}

const useBestFitProduct = ({
  brandName,
  productId,
}: UseBestFitProduct): Return => {
  const currentID = 1;
  return {
    productName: `티트리 트러블 패드-${productId}`,
    productImage: `${IMAGES_PATH}${brandName}/product/${currentID}.png`,
    reason:
      '티트리 트러블 패드는 메디힐이 선보인 네모패드 라인업 중 하나예요. 제주산 티트리에 유산균을 더해 200시간 추출한 티트리 카밍 바이옴이 들어가 확실한 진정 케어가 가능해요. 비건 인증을 받은 얇고 넓은 패드로 완벽 밀착되니 마스크팩처럼 붙여서 사용하기 좋아요.',
  };
};

export default useBestFitProduct;
