import { css } from '@emotion/react';
import { useRouter } from 'next/router';

import { Typography } from 'src/components/dataDisplay';
import { BrandLogo, CollaborationIcon, Logo } from 'src/components/icons';
import { FullWidthBox, Stack } from 'src/components/layout';
import { LinkButton } from 'src/components/navigation';
import { Paper } from 'src/components/surface';
import { BASE_PATH, ROOT_PATH } from 'src/constants/resouce';
import useProductInfo from 'src/hooks/useProductInfo';
import useSurveysForBestFit from 'src/hooks/useSurveysForBestFit';

const paperLayout = css`
  display: flex;
  align-items: center;
  justify-content: center;

  text-align: center;
  color: #ffffff;
`;

const button = css`
  height: 48px;
  padding-top: 12px;
  line-height: 24px;
  padding-bottom: 12px;

  border: none;
  border-radius: 8px;

  color: #ffffff;
  background: #28b978;

  width: 200px;
`;

const removeFirstSlash = (path: string) => {
  if (!path || !path.length) {
    return path;
  }
  const first = path.substring(0, 1);
  if (first !== '/') {
    return path;
  }
  return path.substring(1);
};

const BestFitProductIntroPaper = (): JSX.Element => {
  const router = useRouter();
  const { pathname = '', basePath = '' } = router;
  const { name: brandName, logoUrl } = useProductInfo();
  const { backgroundImage } = useSurveysForBestFit({ brandName });

  const hrefForSurvey = `${ROOT_PATH}${BASE_PATH}${basePath}${removeFirstSlash(
    pathname,
  )}/survey?product_name=${brandName}`;

  return (
    <FullWidthBox>
      <Paper backgroundUrl={backgroundImage} css={paperLayout}>
        <Stack spacing={20} justifyContent="center" alignItems="center">
          <Stack direction="row" spacing={10} alignItems="center">
            <Logo />
            <CollaborationIcon />
            <BrandLogo src={logoUrl} />
          </Stack>
          <span>
            <div>
              <Typography variant="h1">나의 피부관리</Typography>
            </div>
            <div>
              <Typography variant="h1">유형은?</Typography>
            </div>
          </span>
          <LinkButton css={button} href={hrefForSurvey}>
            테스트 시작하기
          </LinkButton>
        </Stack>
      </Paper>
    </FullWidthBox>
  );
};

export default BestFitProductIntroPaper;
