const SHARED = 'navigator.shared';
const CLIPBOARD = 'navigator?.clipboard';
const EXEC_COMMAND_COPY = 'document.execCommand.copy';
const RESULTS = [SHARED, CLIPBOARD, EXEC_COMMAND_COPY];
type Return = (typeof RESULTS)[number];

interface ShareProps {
  title: string;
  text: string;
  url: string;
}

const copyWithExec = (url: string) => {
  return new Promise<boolean>((resolve, reject) => {
    if (!document?.execCommand || !document.queryCommandSupported('copy')) {
      reject(new Error('not supported'));
      return;
    }

    const body = document.querySelector('body');
    if (!body) {
      reject(new Error('no body'));
      return;
    }
    const area = document.createElement('textarea');
    if (!area) {
      reject(new Error('cannot created textarea'));
      return;
    }
    body.appendChild(area);
    area.value = url;
    area.select();
    document.execCommand('copy');
    body.removeChild(area);
    resolve(true);
  });
};

export const share = (data: ShareProps) => {
  return new Promise<Return>((resolve, reject) => {
    if (window?.navigator?.share) {
      window.navigator
        .share(data)
        .then(() => {
          resolve(SHARED);
        })
        .catch((reason) => {
          reject(new Error(`navigator.share ${reason}`));
        });
      return;
    }
    if (
      window?.navigator?.clipboard &&
      window?.navigator?.clipboard?.writeText
    ) {
      window.navigator.clipboard
        .writeText(data.url)
        .then(() => {
          resolve(CLIPBOARD);
        })
        .catch((reason) => {
          reject(new Error(`navigator.clipboard ${reason}`));
        });
      return;
    }

    copyWithExec(data.url)
      .then((result) => {
        if (result) {
          resolve(EXEC_COMMAND_COPY);
          return;
        }
        reject(new Error('failed to copyWithExec'));
      })
      .catch((reason) => {
        reject(new Error(`document.execCommand${reason}`));
      });
  });
};
